  width: 50%;
  transition: all 0.25s ease-in-out;
}

.sm-c-nav__burger > span:nth-of-type(even) {
  left: 50%;
  border-radius: 0 0em 0em 0;
}

.sm-c-nav__burger > span:nth-of-type(odd) {
  left: 1px;
  border-radius: 0em 0 0 0em;
